<template lang="pug">
  .brew-ring-series(fluid)
    b-row
      b-col.brew-ring-series__banner(
        cols="12"
        :class="type"
      )
        span {{ title }}
    b-row
      b-col.brew-ring-series__description(cols="10" offset="1")
        p {{ description }}
    b-row(:no-gutters="true")
      b-col.brew-ring-series__image-wrapper(
        v-for="image in images" :key="image.alias"
        @click="$router.push(`/brew-ring/${image.linkId}`)"
        cols="12" md="4")
        img.brew-ring-series__image-item(
          :src="image.path"
          :alt="image.alias")
        .brew-ring-series__mask
          h2 {{ image.alias }}
</template>
<script>
import brewRingInfo from '@/views/BrewRingSeries/brewRingSeriesInfo';

export default {
  name: 'brewRingSeries',
  computed: {
    brewRingSeries() {
      return brewRingInfo[this.$route.params.type];
    },
    type() {
      return this.$route.params.type
        ? `brew-ring-series__banner--${this.$route.params.type}`
        : '';
    },
    title() {
      return this.brewRingSeries.title;
    },
    images() {
      return this.brewRingSeries.images;
    },
    description() {
      return this.brewRingSeries.description;
    },
  },
};

</script>

<style lang="scss" scoped>
@import '../../scss/_mediaQuery.scss';
@import '../../scss/_variable.scss';
.brew-ring-series {
  &__banner {
    width: 100%;
    height: 400px;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    span {
      text-shadow: 1px 1px 8px rgba(0, 0, 0, 0.5);
      font-size: 30px;
      color: #ffffff;
    }
    &--scratch {
      background-image: url('~assets/marriageBrewRing/introduction/scratch/banner@2x.png');
    }
    &--lava {
      background-image: url('~assets/marriageBrewRing/introduction/lava/banner@2x.png');
    }
    &--engraving {
      background-image: url('~assets/marriageBrewRing/introduction/engraving/banner@2x.png');
      @include tablet-and-mobile-only {
        background-image: url('~assets/marriageBrewRing/introduction/engraving/banner-m@2x.png');
      }
    }
    &--tassel {
      background-image: url('~assets/marriageBrewRing/introduction/tassel/banner@2x.png');
    }
  }
  &__description {
    p {
      font-size: 15px;
      line-height: 1.73;
      letter-spacing: 0.5px;
      text-align: justify;
      color: #3e3a39;
      padding-top: 50px;
      padding-bottom: 50px;
    }
  }
  &__image-wrapper {
    overflow: hidden;
    &:hover {
      .brew-ring-series__mask {
        opacity: 1;
        background-color:rgba(0,0,0,0.7);
      }
    }
  }
  &__image-item {
    position: relative;
    width: 100%;
    height: 100%;
    border: 1px solid white;
    transition: .3s;
    cursor: pointer;
    &:hover {
      transform: scale(1.1);
    }
  }
  &__mask {
    width: 100%;
    height: 100%;
    border: 1px solid white;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    overflow:visible;
    box-sizing:border-box;
    transition: all 0.4s ease-in-out;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    h2 {
      color: white;
    }
  }
}
</style>
